<template>
    <div class="search">
        <div class="container">
            <div class="list-button">
                <router-link to="/movies">
                    <i class="material-icons">account_circle</i>
                </router-link>
            </div>
            <div class="col-md-12 col-lg-8 mx-auto">
                <img src="../assets/logo.svg" class="img-fluid">
            </div>

            <div class="col-md-12 col-lg-8 mx-auto">
                <input type="text" v-model="query" @keyup="getResult(query)" class="search-input">
            </div>

            <!-- Search Results -->
            <div v-if="query != ''" class="col-md-6 mx-auto result-container">
                <table class="table text-left">
                    <tbody v-for="result in results" :key="result.id">
                        <tr>
                            <td class="align-middle">{{result.title}}</td>
                            <!--<td>{{trimDate(result.release_date)}}</td>-->
                            <td class="text-right">
                                <form @submit.prevent="saveMovie">
                                    <input type="hidden" v-bind:value="result.title" name="movie_title">
                                    <input type="hidden" v-bind:value="result.overview" name="movie_overview">
                                    <input type="hidden" v-bind:value="result.poster_path" name="movie_poster_path">
                                    <input type="hidden" v-bind:value="result.vote_average" name="movie_vote">
                                    <input type="hidden" v-bind:value="result.release_date" name="movie_year">
                                    <button type="submit">
                                        <i class="material-icons align-middle add-button">add_box</i>
                                    </button>
                                </form>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <Popular/>
        </div>
    </div>
</template>

<script>

import axios from 'axios'
import db from '@/components/firebase'
import Popular from '@/components/Popular'

export default {
    name: 'search',
    data() {
        return {
            query: '',
            results: '',
        }
    },
    methods: {
        getResult(query) {
            if(query != '') {
                    axios.get('https://api.themoviedb.org/3/search/movie?api_key=9a689916ab6b4849c7bccf3ac57f4eda&query=' + query).then(response => {
                    this.results = response.data.results
                })
            }
        },
        trimDate(date) {
            return date.substring(0,4)
        },
        saveMovie(e) {
            db.settings({ timestampsInSnapshots: true })
            db.collection('movies').add({
                movie_title: e.target.elements.movie_title.value,
                movie_year: e.target.elements.movie_year.value,
                movie_vote: e.target.elements.movie_vote.value,
                movie_overview: e.target.elements.movie_overview.value,
                movie_poster_path: e.target.elements.movie_poster_path.value
            })
            .then(docRef => this.$router.push('/movies'))
            .catch(error => console.log(error))
        }
    },
    components: {
        Popular
    }
}
</script>



