<template>
    <div class="popular">
        <div class="row">
            <div class="col-md-12 col-lg-8 mx-auto text-left">
                <h3 class="popular-head-title">Popular movies</h3>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12 col-lg-8 mx-auto">
                <div class="row">
                    <div v-for="result in results" :key="result.id" class="col-lg-3 col-md-3 col-sm-6 col-6 popular-result">
                        <a target="_blank" v-bind:href="'https://www.themoviedb.org/movie/' + result.id">
                            <img v-bind:src="'http://image.tmdb.org/t/p/w500/' + result.poster_path" width="150px" class="img-thumbnail">
                            <p>{{result.title}}</p>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import axios from 'axios'

export default {
    name: 'search',
    data() {
        return {
            results: '',
        }
    },
    methods: {
        getPopular(query) {
            axios.get('https://api.themoviedb.org/3/movie/popular?page=1&language=en-US&api_key=9a689916ab6b4849c7bccf3ac57f4eda').then(response => {
                this.results = response.data.results
            this.results.length = 8
            })
        }
    },
    created() {
        this.getPopular()
    }
}
</script>



