<template>
    <div id='movies'>
        <div class="container text-left">
            <div class="back-button">
                <router-link to="/">        
                    <i class="material-icons align-middle">arrow_back</i> 
                    <span>Go back</span>
                </router-link>
            </div>
            
            <h1 class="">Watchlist</h1>
            <div class="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                            <th class="text-center" scope="col"></th>
                            <th class="text-left" scope="col">Title</th>
                            <th class="text-left" scope="col">Vote</th>
                            <th class="text-left" scope="col">Year</th>
                            <th class="text-right"scope="col"></th>
                        </tr>
                    </thead>
                    <tbody v-for="movie in movies" :key="movie.id">
                        <tr>
                            <td class="text-center">
                                <img v-bind:src="'http://image.tmdb.org/t/p/w500/' + movie.posterpath" width="100px" class="img-thumbnail">
                            </td>
                            <td class="text-left bold align-middle">
                                {{movie.title}}
                                <br>
                            </td>
                            <td class="text-left align-middle">{{movie.vote}}</td>
                            <td class="text-left align-middle">{{trimDate(movie.year)}}</td>
                            <td class="text-right align-middle">
                                <button @click="deleteMovie(movie.id)">
                                    <i class="material-icons align-middle delete-button">delete_forever</i>
                                </button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>

import db from '@/components/firebase'

export default {
  name: 'movies',
  data() {
      return {
          movies: []
      }
  },
  created() {
      db.settings({ timestampsInSnapshots: true })
      db.collection('movies').orderBy('movie_vote').get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
              const data = {
                'id': doc.id,
                'overview': doc.data().overview,
                'title': doc.data().movie_title,
                'year': doc.data().movie_year,
                'vote': doc.data().movie_vote,
                'posterpath': doc.data().movie_poster_path
              }
              this.movies.push(data)
          })
      })
  },
  methods: {
    deleteMovie(id) {
    db.collection('movies').doc(id).delete()
    this.$router.push('/')
    },
    trimDate(date) {
        return date.substring(0,4)
    },
  }
}
</script>


